@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
    background: linear-gradient(
        92.23deg,
        #ff56f6 21.43%,
        #b936ee 50.63%,
        #3bace2 100%,
        #406aff 117.04%
    );
    box-shadow: 0px 4.42184px 107.23px rgba(255, 86, 246, 0.51);
}

.btn:hover {
    background: linear-gradient(
        92.23deg,
        #406aff 21.43%,
        #3bace2 50.63%,
        #b936ee 100%,
        #ff56f6 117.04%
    );
}

.gradient,
.active {
    background: linear-gradient(
        92.23deg,
        #406aff 21.43%,
        #3bace2 50.63%,
        #b936ee 100%,
        #ff56f6 117.04%
    );
}

.active {
    color: #fff;
    padding: 0.2rem;
    border-radius: 100%;
}

.icont:hover {
    color: #fff;
    padding: 1rem;
    border-radius: 100%;
    background: linear-gradient(
        92.23deg,
        #406aff 21.43%,
        #3bace2 50.63%,
        #b936ee 100%,
        #ff56f6 117.04%
    );
}

@layer base {
    body {
        margin: 0;
        @apply font-secondary text-lg leading-8 text-white;
    }
    .h2 {
        @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
    }
    .h3 {
        @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
    }
    .btn {
        @apply rounded-full font-primary text-white font-medium;
    }
    .btn-sm {
        @apply h-[40px] px-4 text-sm;
    }
    .btn-lg {
        @apply h-[56px] px-10 text-base;
    }
    .btn-link {
        @apply text-base;
    }
    .text-gradient {
        @apply bg-gradient-to-r text-transparent bg-clip-text from-[#42A6E3] to-[#FF56F6] font-bold font-primary hover:from-[#FF56F6] hover:to-[#42A6E3];
    }
    .section {
        @apply py-8 lg:py-24 lg:h-screen flex items-center;
    }
}
.navbar .color-active {
    background-color: white;
    border-bottom: 2px solid var(--secondary-color);
}
.navbar {
    position: fixed;
    z-index: 222;
    transition: all 1s ease;
    width: 100%;
    /* padding: 8px; */
}

.skeleton {
    background: rgba(255, 255, 255, 0.2);
}
